<template>
  <div>bar-chart
    <br>

    <button v-on:click = "show = !show">点我</button>
    <transition name="bounce">
      <p v-if="show">-- 学的不仅是技术，更是梦想！！！</p>
    </transition>

  </div>
</template>

<script>
  export default {
    name: "bar-chart",
    data(){
      return{
        show:false,
      }
    }
  }
</script>

<style scoped>
  .bounce-enter-active {
    animation: bounce-in .5s forwards;
  }
  /*.bounce-leave-active {*/
    /*animation: bounce-in .5s forwards;*/
  /*}*/
  @keyframes bounce-in {
    0% {
      transform: scale(0);
    }
    50% {
      transform: scale(1.5);
    }
    100% {
      transform: scale(1);
    }
  }
</style>



