<template>
  <div>
      <div class="eCharts":class="{move:move===1}">
        <div class="e-title">
          <div class="left">
            <div class="photo">
              <img src="../../static/testphoto.png" alt="">
            </div>
            <span>余晶莹</span>
          </div>
          <div class="right"> 关于</div>
        </div>
        <div :is="eChartsView"></div>
        <i class="iconfont icon-v"></i>
      </div>
    <div class="tab-item">
      <a href="javascript:void(0)" v-for="(tab,index) in tabs" @click='tabChange(index,tab.view,tab.view2)'
         :class="{active:active===index}">{{tab.type}}
        <div class="excircle" :class="{active3:active===index}" >
          <i class="iconfont" :class="[{active2:active===index},tab.class1 ]"></i>
        </div>
        <div :class="{activel:active===index}" class="activel-n">
          <i class="iconfont">&#xe628;</i>
        </div>
      </a>
    </div>
    <div class="xxx1" :class="{move2:move===1}">
      <v-touch v-on:swipeup="swiperup" class="wrapper">
      <div :is="currentView"></div>
      </v-touch>
      <div class="xx2" v-if="!show2">您被3家企业认可，获得3个特权机会，上滑查看</div>
      <i class="iconfont shang-ic" v-if="!show2"> &#xe672; </i>
      <div class="xxx3" v-if="show2">
        <div class="tab-tatle">我的特权机会</div>
        <div class="tabCard">
          H 140
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import BarChart from '@/components/demo/BarChart';
  import PieChart from '@/components/demo/PieChart';
  import RadarChart from '@/components/demo/RadarChart';
  import InfoBar from '@/components/demo/InfoBar';
  import InfoPie from '@/components/demo/InfoPie';
  import InfoRadar from '@/components/demo/InfoRadar';

  export default {
    name: "ocean",
    data() {
      return {
        currentView: 'InfoBar', // 默认选中第一项
        eChartsView: 'BarChart',
        active: 0,
        move:0,
        eCheight: 60, //echarts 高度
        show:true,
        show2:false,
        tabs: [
          {
            type: '能力画像',
            view: 'InfoBar',
            view2: 'BarChart',
            class1:'icon-zhengjian'
          },
          {
            type: '能力图谱',
            view: 'InfoPie',
            view2: 'PieChart',
            class1:'icon-dengpao1'
          },
          {
            type: '职业匹配',
            view: 'InfoRadar',
            view2: 'RadarChart',
            class1:'icon-zhiyeshengyamubiao'
          }
        ]
      }
    },
    components: {
      BarChart,  //柱状
      PieChart,  //饼状
      RadarChart, //雷达
      InfoBar,
      InfoPie,
      InfoRadar
    },
    methods: {
      tabChange(i, tab1, tab2) {
        this.active = i
        this.currentView = tab1;
        this.eChartsView = tab2;
        this.move = 0;
        this.show2 = false;
      },
      xx() {
        console.log('21212')
        this.move = 1;
      },
      swiperup: function () {
        // this.$router.push({'path':'/queuehistory'});
        console.log('21212')
        this.move = 1;
        this.show2 = true;
      },
      // swiperright: function () {
      //   // this.$router.push({'path':'/home'});
      //   console.log('21212')
      // }
    },
    lableClass(){

    }
  }
</script>

<style scoped>
  .tab-item {
    height: 44px;
    border-bottom:1px solid #01C7D8;
    display: flex;
    justify-content: space-evenly;
    position: relative;
    font-size: 12px;
    width: 90vw;
    margin: 0 auto;
  }
  .excircle {
    height: 36px;
    width: 36px;
    border: #fff solid 1px;
    border-radius: 50%;
    position: absolute;
    left: 8px;
    top: -38px;
    background: #ffffff;
    padding-top: 4px;
    box-sizing: border-box;
  }
  .excircle .iconfont{
    color: #01C7D8;
    font-size: 22px;
  }
  .xx2 {
    position: absolute;
    bottom: 38px;
    font-size: 14px;
    color: #ec40bb;
    left: 0;
    right: 0;
  }
  .shang-ic {
    position: absolute;
    bottom: 14px;
    color: #ec40bb;
  }
  .active2 {
    color: #ffffff!important;
  }
  .active3 {
    background:#01C7D8;
  }
  .tab-item a {
    position: relative;
    top: 18px;
    color: #999999;
  }
  .activel-n {
    display: none;
  }
  .activel-n i {
    font-size: 22px;
    margin-left: -3px;
  }
  .activel {
   display: block;
    color: #01C7D8 ;
    background: #ffffff;
    width: 15px;
    margin: 0 auto;
    margin-top: -6px;
  }
  .xxx3 {
    height: 24vh;
    background: #038791;
    position: relative;
    padding-top: 24px;
    box-sizing: border-box;
  }
  .xxx1 {
    height: calc(40vh - 42px);
    /*height: 40vh;*/
    border-bottom: 1px solid red;
    /*background: rebeccapurple;*/
    position: relative;
    transition: 500ms;
  }
  .active {
    color: #000000!important;
  }
.tabCard {
  height: 140px;
  border: 1px solid;
}
  .eCharts {
    height: 60vh;
    background: linear-gradient(to top right, #55beee, #6e69db);
    transition: 500ms;
  }

  .e-title {
    height: 44px;
    display: flex;
    justify-content: space-between;
    line-height: 44px;
    color: white;
    width: 90vw;
    border-bottom: 1px solid rgba(255, 255, 255, .4);
    margin: 0 auto;
  }

  .e-title .right {
    font-size: 12px;
  }

  .e-title .left {
    display: flex;
    font-size: 14px;
  }

  .e-title .photo {
    width: 24px;
    border: 1px solid #fff;
    height: 24px;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 5px;
    margin-top: 8px;
  }

  .e-title .photo img {
    width: 24px;
    vertical-align: top;
  }
  @keyframes bounce-in {
    0% {
      height: 60vh;
    }
    100% {
      height: 50vh;
    }
  }
  /*@-webkit-keyframes myfirst !* Safari 和 Chrome *!*/
  /*{*/
    /*from {height: 60vh;}*/
    /*to {height: 50vh;}*/
  /*}*/
  /*@-webkit-keyframes myfirst2 !* Safari 和 Chrome *!*/
  /*{*/
    /*from {height: calc(40vh - 42px)}*/
    /*to {height: calc(26vh - 42px)}*/
  /*}*/

  .move{
    /*animation: myfirst .5s forwards;*/
    /*-moz-animation: myfirst .5s;	!* Firefox *!*/
    /*-webkit-animation: myfirst .5s forwards;	!* Safari 和 Chrome *!*/
    /*-o-animation: myfirst .5s;	!* Opera *!*/
    height: 50vh;
  }
  .move2{
    /*animation: myfirst2 .5s forwards;*/
    /*-moz-animation: myfirst2 .5s;	!* Firefox *!*/
    /*-webkit-animation: myfirst2 .5s forwards;	!* Safari 和 Chrome *!*/
    /*-o-animation: myfirst2 .5s;	!* Opera *!*/
    height: calc(26vh - 42px);
  }
  .wrapper {
    height: 100%;
    /*background: salmon;*/
  }
  .tab-tatle {
    width: 122px;
    height: 22px;
    color: white;
    background: #D134A4;
    border-radius: 22px;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    top: -10px;
  }

</style>



